<template>
  <div class="page trialpreson flex-v trialdoctor">
    <div class="headertitle flex">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb ">
        <el-breadcrumb-item :to="{name:'reviewerstatistics'}">复查人员统计</el-breadcrumb-item>
        <el-breadcrumb-item>更多</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!-- 复查人员 -->
    <div class="table-box flex-item"
      style="padding-top:20px;">
      <el-table stripe
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        height='100%'
        header-cell-class-name='custom-table-cell'>
        <el-table-column type="index"
          width="50"
          label="序号">
          <template v-slot="scope">
            {{scope.$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="sysGuid"
          label="个人登记流水号"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="center"
          prop="userName"
          label="姓名">
        </el-table-column>
        <el-table-column align="center"
          label="性别"
          width="60">
          <template slot-scope="scope">
            <div v-if="scope.row.sexType==1">男</div>
            <div v-else-if="scope.row.sexType==2">女</div>
            <div v-else>不详</div>
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="age"
          label="年龄"
          width="60">
        </el-table-column>
        <el-table-column align="center"
          prop="examNum"
          label="体检次数"
          width="80">
        </el-table-column>
        <el-table-column align="center"
          prop="twoAuthUserName"
          label="医生姓名"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="center"
          prop="createOrganizationName"
          label="机构名称"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="center"
          prop="yearsold"
          label="登记时间"
          width="120">
          <template slot-scope="scope">
            <div>{{scope.row.recordTime | formatDate}}</div>
          </template>
        </el-table-column>
        <el-table-column align="center"
          label="复查时间"
          width="120">
          <template slot-scope="scope">
            <div>{{scope.row.recheckTime | formatDate}}</div>
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="recheckContent"
          label="复查内容"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="center"
          label="重点类型"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div>{{scope.row.personType | getBoxType}}</div>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="$route.query"
        :query-url="queryUrl"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="loading=$event" />
    </div>
  </div>
</template>
<script>
import { calcType } from '@/lib/utils'
export default {
  name: 'reviewerstatisticsmore',
  data() {
    return {
      queryUrl: '/charts/base/getReviewUserList',
      pageNum: 1,
      pageSize: 10,
      gobackpath: '',
      trialpagestatic: false,
      totalpagestatic: false,
      reviewerstatic: false,
      tableData: [],
      organizationlist: [],
      loading: true
    }
  },
  created() {},
  methods: {
    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    }
  },
  filters: {
    getBoxType: function(val) {
      let { nameList } = calcType(val)
      return nameList ? nameList.join('/') : '未分配'
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.trialdoctor {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .rightbtn {
    display: flex;
    .marginright50 {
      margin-right: 50px;
    }
    div {
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #666666;
      span {
        width: 25px;
        height: 60px;
      }
    }
  }
  .countlist {
    margin-bottom: 20px;
    .countlist-item {
      width: 300px;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      justify-content: center;
      flex-direction: column;
      padding: 0 30px;
      margin-right: 20px;
      .itemtitle {
        font-size: 14px;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
      }
    }
    .countlist-item:last-child {
      margin-right: 20px;
    }
  }
}
</style>

